import { Action, createReducer, on } from '@ngrx/store';
import update from 'immutability-helper';

import { initialAuthState, Auth } from '@root-store/auth/state';

import * as AuthActions from '@root-store/auth/actions/auth.actions';

const authReducer = createReducer(
  initialAuthState,

  on(
    AuthActions.auth,
    AuthActions.signUp,
    AuthActions.chechAuthByToken,

    (state) =>
      update(state, {
        isAuthenticated: { $set: false },
        isFetching: { $set: true },
        errorMessage: { $set: null },
      })
  ),

  on(
    AuthActions.authSuccess,
    AuthActions.signUpSuccess,
    AuthActions.chechAuthByTokenSuccess,

    (state, { payload }) =>
      update(state, {
        data: { $set: payload },
        isAuthenticated: { $set: true },
        isFetching: { $set: false },
        errorMessage: { $set: null }
      })
  ),

  on(
    AuthActions.authFailure,
    AuthActions.signUpFailure,
    AuthActions.chechAuthByTokenFailure,

    (state, { payload: { error } }) =>
      update(state, {
        isAuthenticated: { $set: false },
        isFetching: { $set: false },
        errorMessage: { $set: error.message }
      })
  ),

  on(AuthActions.logOut, (() => initialAuthState))
);

export function reducer(state: Auth, action: Action) {
  return authReducer(state, action);
}
