import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Actions, ofType, Effect } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';

import { AppState } from '@root-store/state';
import { ColumnService } from '@app/core/services/column.service';
import { CustomActionType } from '@app/models/action';
import * as BoardActions from '@root-store/board/actions/board.actions';

@Injectable()
export class ColumnEffects {

  constructor(
    private actions$: Actions,
    private store: Store<AppState>,
    private columnService: ColumnService
  ) { }

  @Effect()
  createColumn$ = this.actions$.pipe(
    ofType(BoardActions.createColumn),
    switchMap(({ payload }: CustomActionType<{boardId: string, columnTitle: string, templateColumnId: string}>) => {
      const { boardId, columnTitle } = payload;

      return this.columnService.createColumn(boardId, columnTitle)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.createColumnFailure({ payload: error }));
          })
        )
    })
  );

  @Effect()
  deleteColumn$ = this.actions$.pipe(
    ofType(BoardActions.deleteColumn),
    switchMap(({ payload }: CustomActionType<string>) => {
      return this.columnService.deleteColumn(payload).pipe(
        map(() => {
          return BoardActions.deleteColumnSuccess({ payload });
        }),
        catchError((error: HttpErrorResponse) => {
          console.error(error);
          return of(BoardActions.deleteColumnFailure({ payload: error }));
        })
      );
    })
  );

  @Effect()
  updateColumnTitle$ = this.actions$.pipe(
    ofType(BoardActions.updateColumnTitle),
    switchMap(({ payload }: CustomActionType<{ title: string, columnId: string }>) => {
      const { title, columnId } = payload;
      return this.columnService.updateColumnTitle(columnId, title).pipe(
        map(() => {
          return BoardActions.updateColumnTitleSuccess({ payload: { title, columnId } });
        }),
        catchError((error: HttpErrorResponse) => {
          console.error(error);
          return of(BoardActions.updateColumnTitleFailure({ payload: error }));
        })
      );
    })
  );
}
