import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, switchMap, take, catchError } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { Actions, ofType, Effect } from '@ngrx/effects';

import { AppState } from '@root-store/state';

import { TaskService } from '@app/core/services/task.service';
import { CustomActionType } from '@app/models/action';
import { Board } from '@app/models/board';
import { Task } from '@app/models/task';

import * as BoardActions from '@root-store/board/actions/board.actions';

import { getBoard } from '@root-store/board/selectors/board.selectors';

@Injectable()
export class TaskEffects {

  constructor(
    private actions$: Actions,
    private store: Store<AppState>,
    private taskService: TaskService
  ) {
  }

  @Effect()
  createTask$ = this.actions$.pipe(
    ofType(BoardActions.createTask),
    switchMap(({payload}: CustomActionType<{columnId: string, task: string, boardId: string}>) => {
      const { columnId, task, boardId } = payload;

      return this.taskService.createTask(columnId, task)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.createTaskFailure({ payload: error }));
          })
        )
    })
  );

  @Effect()
  deleteTask$ = this.actions$.pipe(
    ofType(BoardActions.deleteTask),
    switchMap(({payload}: CustomActionType<{taskId: string, boardId: string}>) => {
      const { taskId, boardId } = payload;

      return this.taskService.deleteTask(taskId)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.deleteTaskFailure({ payload: error }));
          })
        )
    })
  );

  @Effect()
  updateTaskTitle$ = this.actions$.pipe(
    ofType(BoardActions.updateTaskTitle),
    switchMap(({payload}: CustomActionType<{task: string, taskId: string, boardId: string}>) => {
      const { task, taskId, boardId } = payload;

      return this.taskService.updateTaskTitle(taskId, task)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.updateTaskTitleFailure({ payload: error }));
          })
        )
    })
  );

  @Effect()
  updateTaskMarks$ = this.actions$.pipe(
    ofType(BoardActions.updateTaskMarks),
    switchMap(({payload}) => {
      const { mark, taskId, boardId } = payload;

      return this.taskService.updateTaskMarks(taskId, mark)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.updateTaskMarksFailure({ payload: error }));
          })
        )
    })
  );

  @Effect()
  changeTaskStatus$ = this.actions$.pipe(
    ofType(BoardActions.changeTaskStatus),
    switchMap(({payload}) => {
      const { taskId, status, boardId } = payload;

      return this.taskService.changeTaskStatus(taskId, status)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.changeTaskStatusFailure({ payload: error }));
          })
        )
    })
  );

}
