import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { catchError, map, switchMap, tap } from 'rxjs/operators';

import { AuthService } from '@app/core/services/auth.service';
import { CustomActionType } from '@app/models/action';
import { AuthDataRequest, AuthResponse, SignUpDataRequest } from '@app/models/auth';
import { Response } from '@app/models/response';

import * as AuthActions from '@root-store/auth/actions/auth.actions';
import * as BoardsActions from '@root-store/boards/actions/boards.actions';

@Injectable()
export class AuthEffects {

  constructor(
    private actions$: Actions,
    private router: Router,
    private authService: AuthService
  ) { }

  @Effect()
  logIn$ = this.actions$.pipe(
    ofType(AuthActions.auth),
    switchMap(({payload}: CustomActionType<AuthDataRequest>) => {
      return this.authService.logIn(payload)
        .pipe(
          map((response: Response<AuthResponse>) => {
            localStorage.setItem('sessionToken', response.data.token);
            return AuthActions.authSuccess({payload: response.data});
          }),
          catchError((error: HttpErrorResponse) => {
            return of(AuthActions.authFailure({payload: error}));
          })
        );
    })
  );

  @Effect()
  signUp$ = this.actions$.pipe(
    ofType(AuthActions.signUp),
    switchMap(({ payload }: CustomActionType<SignUpDataRequest>) => {
      return this.authService.signUp(payload)
        .pipe(
          map((response: Response<AuthResponse>) => {
            return AuthActions.signUpSuccess({ payload: response.data});
          }),
          catchError((error) => {
            console.error(error);
            return of(AuthActions.signUpFailure({ payload: error }));
          })
        );
    })
  );

  @Effect({ dispatch: false })
  logInSuccess$ = this.actions$.pipe(
    ofType(AuthActions.authSuccess, AuthActions.signUpSuccess),
    tap(() => this.router.navigate(['/']))
  );

  @Effect()
  checkAuthByToken$ = this.actions$.pipe(
    ofType(AuthActions.chechAuthByToken),
    switchMap(() => {
      return this.authService.checkLogin().pipe(
        map((response: Response<AuthResponse>) => {
          return AuthActions.chechAuthByTokenSuccess({ payload: response.data});
        }),
        catchError((error) => {
          console.error(error);
          return of(AuthActions.chechAuthByTokenFailure({ payload: error }));
        })
      );
    })
  );

  @Effect()
  chechAuthByTokenSuccess$ = this.actions$.pipe(
    ofType(AuthActions.chechAuthByTokenSuccess),
    map(() => BoardsActions.loadBoards())
  );

  @Effect({ dispatch: false })
  logOut$ = this.actions$.pipe(
    ofType(AuthActions.logOut),
    tap(() => {
      localStorage.removeItem('sessionToken');
      return this.router.navigate(['/sign-in']);
    })
  );
}
