import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ActionReducer, MetaReducer, StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { routerReducer, RouterState, StoreRouterConnectingModule } from '@ngrx/router-store';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';

import { environment } from '@env/environment';

import { AppState, initialAppState } from '@root-store/state';
import { logOut } from '@root-store/auth/actions/auth.actions';
import { AuthModule } from '@root-store/auth/auth.module';
import { BoardsModule } from '@root-store/boards/boards.module';
import { BoardModule } from '@root-store/board/board.module';
import { PermissionsModule } from '@root-store/permissions/permissions.module';

export function logOutClear(reducer: ActionReducer<AppState>): ActionReducer<AppState> {
  return (state, action) => {
    if (logOut().type === action.type) {
      return reducer({ ...initialAppState, router: state.router}, action)
    }

    return reducer(state, action);
  };
}

export const metaReducers: MetaReducer<any>[] = [logOutClear];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    AuthModule,
    BoardsModule,
    BoardModule,
    PermissionsModule,

    StoreModule.forRoot({router: routerReducer}, {
      runtimeChecks: {
        strictStateImmutability: true,
      },
      metaReducers
    }),
    EffectsModule.forRoot([]),
    StoreRouterConnectingModule.forRoot({ stateKey: 'router', routerState: RouterState.Minimal }),
    !environment.production ? StoreDevtoolsModule.instrument() : []
  ]
})
export class RootStoreModule { }
