import { Directive, Output, EventEmitter, Input, HostListener } from '@angular/core';

@Directive({
  selector: '[appAllowClick]'
})
export class AllowClickDirective {
  @Output() allowClick = new EventEmitter();
  @Input() appAllowClick: boolean;
  constructor() { }

  @HostListener('click', ['$event'])
  clickEvent() {
    if (this.appAllowClick) {
      this.allowClick.emit();
    }
  }

}
