import { Action, createReducer, on } from '@ngrx/store';
import { moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import update from 'immutability-helper';

import { BoardState, initialState } from '@root-store/board/state';
import * as BoardActions from '@root-store/board/actions/board.actions';
import { Board } from '@app/models/board';
import { Column } from '@app/models/column';
import { Task } from '@app/models/task';

const boardReducer = createReducer(
  initialState,

  on(
    BoardActions.loadBoard,
    BoardActions.updateBoardTitle,
    BoardActions.createColumn,
    BoardActions.deleteColumn,
    BoardActions.deleteBoard,
    BoardActions.updateColumnTitle,
    BoardActions.createTask,
    BoardActions.deleteTask,
    BoardActions.updateTaskTitle,
    BoardActions.updateTaskMarks,

    (state) =>
      update(state, {
        errorMessage: { $set: null },
        isFetching: { $set: true },
      })
  ),

  on(
    BoardActions.createTaskComment,
    BoardActions.deleteTaskComment,
    BoardActions.changeTaskStatus,

    (state) =>
      update(state, {
        errorMessage: { $set: null },
        isFetching: { $set: true },
      })
  ),

  on(BoardActions.loadBoardSuccess, (state, { payload }) =>
    update(state, {
      board: { $set: payload },
      errorMessage: { $set: null },
      isFetching: { $set: false }
    })
  ),

  on(
    BoardActions.loadBoardFailure,
    BoardActions.updateBoardTitleFailure,
    BoardActions.createColumnFailure,
    BoardActions.deleteColumnFailure,
    BoardActions.deleteBoardFailure,
    BoardActions.updateColumnTitleFailure,
    BoardActions.createTaskFailure,
    BoardActions.deleteTaskFailure,
    BoardActions.updateTaskTitleFailure,
    BoardActions.updateTaskMarksFailure,

    (state, { payload: { error } }) =>
      update(state, {
        errorMessage: { $set: error.message },
        isFetching: { $set: false }
      })
  ),

  on(
    BoardActions.createTaskCommentFailure,
    BoardActions.deleteTaskCommentFailure,
    BoardActions.changeTaskStatusFailure,

    (state, { payload: { error } }) =>
      update(state, {
        errorMessage: { $set: error.message },
        isFetching: { $set: false }
      })
  ),

  on(BoardActions.updateBoardTitleSuccess, (state, { payload }) =>
    update(state, {
      board: (board: Board) => update(board, {
        title: { $set: payload }
      }),
      isFetching: { $set: false },
      errorMessage: { $set: null },
    })
  ),


  on(BoardActions.updateBoardState, (state, { payload }) =>
    update(state, {
      board: { $set: payload },
      errorMessage: { $set: null },
      isFetching: { $set: false }
    })
  ),

  on(BoardActions.deleteBoardSuccess, (state) =>
    update(state, {
      errorMessage: { $set: null },
      isFetching: { $set: false }
    })
  ),

  on(BoardActions.deleteColumnSuccess, (state, { payload }) =>
    update(state, {
      board: (board: Board) => update(board, {
        columns: columns => columns.reduce((previousValue, currentValue) => {
          if (currentValue._id !== payload) {
            previousValue.push(currentValue)
          }

          return previousValue;
        }, [])
      }),
      errorMessage: { $set: null },
      isFetching: { $set: false }
    })
  ),

  on(BoardActions.updateColumnTitleSuccess, (state, { payload }) =>
    update(state, {
      board: (board: Board) => update(board, {
        columns: columns => columns.map(column => {
          if (column._id === payload.columnId) {
            return { ...column, title: payload.title }
          }

          return column;
        })
      }),
      errorMessage: { $set: null },
      isFetching: { $set: false }
    })
  ),

  on(BoardActions.updateTaskMarks, (state, { payload }) => {
    const { columnId, taskId, mark } = payload;

    return update(state, {
      board: (board: Board) => update(board, {
        columns: (columns: Column[]) => columns.map(column => {
          if (columnId === column._id) {
            return update(column, {
              tasks: (tasks: Task[]) => tasks.map(task => {
                if (taskId === task._id) {
                  return update(task, {
                    marks: (marks: string[]) => {
                      const isCheckedMark = marks.some((checkMark) => checkMark === mark);

                      if (isCheckedMark) {
                        return marks.filter((checkMark) => checkMark !== mark);
                      }

                      return [...marks, mark];
                    }
                  })
                }
                return task
              })
            })
          }
          return column;
        })
      }),
      errorMessage: { $set: null },
      isFetching: { $set: false }
    });
  }),

  on(BoardActions.dragAndDrop, (state, {payload}) =>
    update(state, {
      board: (board: Board) => update(board, {
        columns: (columns: Column[]) => {
          const columnSource = columns.find((column => column._id === payload.source.droppableId));
          const columnDestination = columns.find((column => column._id === payload.destination.droppableId));
          const tasksSource = [...columnSource.tasks];
          const tasksDestination = [...columnDestination.tasks];

          if (payload.source.droppableId === payload.destination.droppableId) {
            moveItemInArray<Task>(tasksDestination, payload.source.index, payload.destination.index);
          } else {
            transferArrayItem<Task>(tasksSource, tasksDestination, payload.source.index, payload.destination.index);
          }

          return columns.map((column) => {
            if (column._id === payload.destination.droppableId) {
              return update(column, {
                tasks: { $set: tasksDestination }
              })
            }

            if (column._id === payload.source.droppableId) {
              return update(column, {
                tasks: { $set: tasksSource }
              })
            }

            return column;
          });
        }
      }),
      errorMessage: { $set: null },
      isFetching: { $set: true }
    })
  )
);

export function reducer(state: BoardState | undefined, action: Action) {
  return boardReducer(state, action);
}
