import { createSelector } from '@ngrx/store';

import { AppState } from '@root-store/state';
import { BoardsState } from '@root-store/boards/state';
import { Boards } from '@app/models/boards';

const selectBoards = (state: AppState) => state.boards;

export const getBoardsState = createSelector(
  selectBoards,
  (boardsState: BoardsState): BoardsState => {
    return boardsState;
  }
);

export const getBoards = createSelector(
  selectBoards,
  ({ boards }: BoardsState): Boards => {
    return boards;
  }
);
