import { RouterReducerState } from '@ngrx/router-store';

import * as auth from '@root-store/auth/state';
import * as boardsState from '@root-store/boards/state';
import * as boardState  from '@root-store/board/state';

export interface AppState {
  readonly router?: RouterReducerState;
  readonly auth: auth.Auth;
  readonly boards: boardsState.BoardsState;
  readonly board: boardState.BoardState;
}

export const initialAppState: AppState = {
  auth: auth.initialAuthState,
  boards: boardsState.initialState,
  board: boardState.initialState,
};
