import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { of } from 'rxjs';
import { map, switchMap, catchError } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { Actions, ofType, Effect } from '@ngrx/effects';

import { AppState } from '@root-store/state';
import { CommentService } from '@app/core/services/comment.service';
import { CustomActionType } from '@app/models/action';
import { Comment } from '@app/models/comment';
import * as BoardActions from '@root-store/board/actions/board.actions';

@Injectable()
export class CommentEffects {

  constructor(
    private actions$: Actions,
    private store: Store<AppState>,
    private commentService: CommentService
  ) {
  }

  @Effect()
  createTaskComment$ = this.actions$.pipe(
    ofType(BoardActions.createTaskComment),
    switchMap(({payload}: CustomActionType<{comment: Comment, taskId: string, boardId: string}>) => {
      const { taskId, comment, boardId } = payload;

      return this.commentService.createTaskComment(taskId, comment)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.createTaskCommentFailure({ payload: error }));
          })
        );
    })
  );

  @Effect()
  deleteTaskComment$ = this.actions$.pipe(
    ofType(BoardActions.deleteTaskComment),
    switchMap(({payload}: CustomActionType<{commentId: string, taskId: string, boardId: string}>) => {
      const { taskId, commentId, boardId } = payload;

      return this.commentService.deleteTaskComment(taskId, commentId)
        .pipe(
          map(() => {
            return BoardActions.loadBoard({ payload: boardId });
          }),
          catchError((error: HttpErrorResponse) => {
            console.error(error);
            return of(BoardActions.deleteTaskCommentFailure({ payload: error }));
          })
        );
    })
  );
}
