import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, switchMap, take } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { Actions, ofType, Effect } from '@ngrx/effects';

import { BoardsService } from '@app/core/services/boards.service';

import { CustomActionType } from '@app/models/action';
import { AppState } from '@root-store/state';
import { Response } from '@app/models/response';
import { Boards } from '@app/models/boards';

import * as BoardsActions from '@root-store/boards/actions/boards.actions';

import { getBoards } from '@root-store/boards/selectors/boards.selectors';
import { Board } from '@app/models/board';

@Injectable()
export class BoardsEffects {

  constructor(
    private actions$: Actions,
    private boardsService: BoardsService,
    private store: Store<AppState>
  ) { }

  @Effect()
  createBoard$ = this.actions$.pipe(
    ofType(BoardsActions.createBoard),
    switchMap((actionData: CustomActionType<string>) =>
      this.boardsService.createBoard(actionData.payload).pipe(
        map(({ data }: Response<Board>) => {
          return BoardsActions.createBoardSuccess({ payload: data} );
        })
      )
    )
  );

  @Effect()
  checkBoards$ = this.actions$.pipe(
    ofType(BoardsActions.checkBoards),
    switchMap(() => {
      return this.checkStoreBoards().pipe(
        map((boards: Boards | null) => {
          if (boards) {
            return BoardsActions.checkBoardsSuccess();
          } else {
            return BoardsActions.loadBoards();
          }
        })
      );
    })
  );

  @Effect()
  loadBoards$ = this.actions$.pipe(
    ofType(BoardsActions.loadBoards),
    switchMap(() => {
      return this.boardsService.getBoards().pipe(
        map(({ data }: Response<Boards>) => {
          return BoardsActions.loadBoardsSuccess({payload: data});
        }),
        catchError((error: HttpErrorResponse) => {
          console.log(error);
          return of(BoardsActions.loadBoardsFailure({payload: error}));
        })
      );
    })
  );

  checkStoreBoards(): Observable<Boards> {
    return this.store.select(getBoards).pipe(take(1));
  }

}
