import { Action, createReducer, on } from '@ngrx/store';
import update from 'immutability-helper';

import * as BoardsActionTypes from '@root-store/boards/actions/boards.actions';
import { initialState, BoardsState } from '@root-store/boards/state';

const boardsReducer = createReducer(
  initialState,

  on(BoardsActionTypes.loadBoards, (state) =>
    update(state, {
      boards: { $set: null },
      isFetching: { $set: true },
      errorMessage: { $set: null }
    })
  ),

  on(BoardsActionTypes.loadBoardsSuccess, (state, { payload }) =>
    update(state, {
      boards: { $set: payload },
      isFetching: { $set: false },
      errorMessage: { $set: null }
    })
  ),

  on(BoardsActionTypes.loadBoardsFailure, (state, { payload: { error } }) =>
    update(state, {
      isFetching: { $set: false },
      errorMessage: { $set: error.message }
    })
  ),

  on(BoardsActionTypes.openBoardCreator, (state) =>
    update(state, {
      isOpenBoardCreator: { $set: true }
    })
  ),

  on(BoardsActionTypes.closeBoardCreator, (state) =>
    update(state, {
      isOpenBoardCreator: { $set: false }
    })
  ),

  on(BoardsActionTypes.createBoard, (state) =>
    update(state, {
      isFetching: { $set: true }
    })
  ),

  on(BoardsActionTypes.createBoardSuccess, (state, data) =>
    update(state, {
      boards: boards => update(boards, {
        admin: { $push: [data.payload]}
      }),
      isFetching: { $set: false }
    })
  ),

  on(BoardsActionTypes.createBoardFailure, (state, { payload: { error } }) =>
    update(state, {
      isFetching: { $set: false },
      errorMessage: { $set: error.message },
    })
  ),
);

export function reducer(state: BoardsState | undefined, action: Action) {
  return boardsReducer(state, action);
}
